@import "../core/core";
.form {
  margin: 10px 0 30px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include tablet {
    margin: 50px 0 30px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
